import './Attractions.scss'

import { Card } from 'react-bootstrap'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";

import Posts from '../../Datafiles/Attraction.json'
import { Link } from 'react-router-dom';

export const Attractions = () => {

	const settings = {
		dots: true,
		arrows: false,
		infinite: true,		
		autoplay: true,
    speed: 500,
    slidesToShow: 3,
		slidesToScroll: 3,
		responsive: [
			{
				breakpoint: 1024,
				settings: {
					slidesToShow: 2,
					slidesToScroll: 2,					
				}
			},
			{
				breakpoint: 768,
          settings: {
					slidesToShow: 1,
					slidesToScroll: 1
				}
			}
			
			]
	}; 
	
	return (
		
		<div className='attraction'>		
			<h2 className='attraction__title'> </h2>
			<div className='attraction__text'>   ,      ,    .         ,        .<br />
				    20 .  ,      .        14      !              .<br />  ,          .       ,  -,    .     ,      .<br />
				     ,       .     ,    . .       ,          .<br />
				           ,   1804 .    .</div>
			<Slider {...settings}>
				{
					Posts.map(({ id, title, url }) => 
					<Link className='attraction__list' key={id} to={`/attractions/${id}`}>
					<li key={id} className='slick-wrapper attraction'>
						<Card className="bg-dark text-white attraction__post">
						<Card.Img src={url} alt="Card image" className='attraction__post__image'/>
						<Card.ImgOverlay>
							<div className='attraction__text-block'>
								<Card.Title className='attraction__text-block__title'>{title}</Card.Title>							
							</div>
							
							<div className='attraction__text-block__text-more'>
								
								<Card.Text href="#" className='attraction__text-block__text'></Card.Text>
								<Card.Text href="#" className='attraction__text-block__more'>&#10230;</Card.Text>
							</div>
							
						</Card.ImgOverlay>			
						</Card>
					</li>
					</Link>
				
			)}	

			</Slider>
			<Card.Link className='attraction__btn' href="/attractions">   &#10230;</Card.Link>			
			</div>
		
		
	)
}